@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: 'Lato', 'Helvetica Neue', sans-serif;
  background-color: #F4F4F7;
}

h1 {
  font-family: 'Anton', sans-serif;
}

a.link-unstyled {
  color: inherit;
}

a.link-unstyled:hover {
  color: inherit;
  text-decoration: none;
}

.btn {
  padding: 0.375rem 1rem;
  font-size: 1.4rem;
}

.btn.btn-primary {
  color: black;
  background-color: #4CA1AF;
  border-color: #4CA1AF;
  margin-bottom: 1rem;
}

.btn.btn-sat {
  color: black;
  background-color: #A9CCE3;
  border-color: #BFC9CA;
}

.btn.btn-outline-light {
  color: black;
  background-color: #CCD1D1;
  border-color: #BFC9CA;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
  color: black;
  background-color: #D1F2EB;
  border-color: #BFC9CA;
}
.btn.btn-sat:not(:disabled):not(.disabled):hover {
  color: black;
  background-color: #D4E6F1;
  border-color: #BFC9CA;
}
.btn.btn-outline-light:not(:disabled):not(.disabled):hover {
  color: black;
  background-color: #E5E8E8;
  border-color: #BFC9CA;
}
/* 
.btn.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #A3E4D7;
  border-color: #A3E4D7;
} */

.btn.btn-primary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem #D5DBDB;
}




.cover-container {
    width: 100%;
    object-fit: contain;
    /* box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.6); */
    display: flex;
    background: linear-gradient(to right, #0f2027 60%, #203a43, #2c5364);
    height: 100vh;
}

.cover-video {
    object-fit: cover;
    width: 33%;
    overflow: hidden;
    position: absolute;
    z-index: 1;
    height: 92vh;
}

.cover-title {
    color: #B2BABB; 
    padding-top: 4rem;
    padding-left: 5rem;
    width: 40%;
    z-index: 2;
}


.cover-servicios {
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 100vh;
}

.cover-servicios__details {
    text-align: right;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}
.cover-servicioTecnico {
    color: #B2BABB;
    padding-top: 3.8rem;
}

.cover-image {
    width: 61rem;
    opacity: 0.2;
    border-radius: 10px;
    border: #203a43 solid 1px;
}

.cover-servicios_titles {
    color: white;
    position: absolute;
}


@media only screen and (max-width: 960px){
    .cover-container {
        flex-direction: column;
        height: 300vh;
    }
    .cover-video {
        width: 100%;
    }
    .cover-servicios {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .cover-servicioTecnico {
        padding-top: 90rem;
    }
    .cover-servicios__details {
        flex-direction: column;
        width: 100%;
    }
    .cover-image {
        overflow: hidden;
        opacity: 0.2;
        border-radius: 10px;
        border: #203a43 solid 1px;
        object-fit: inherit;
        width: inherit;
        height: 28vh;
    }
    .cover-servicios_titles{
        font-size: .8rem;
        padding: 25px;
    }
    .cover-servicios_titles h3 {
        font-size: 1rem;
    }
    .cover-servicios_titles h5 {
        font-size: .7rem;
    }
}
#ServicioTecnico {
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-left: 5.7rem;
    padding-top: 3rem;
} 

.ServicioTecnico-title {
    color: #B2BABB;;
    padding-bottom: 1rem;
    padding-right: 1rem;
}


* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
#HomePage-container {
  width: 100%;
  height: 100%;
} 
#HomePage-cards {
  padding-left: 11rem;
  margin-bottom: 3rem;
}
#navbarX {
    width: 100%;
    height:11vh;
    background: linear-gradient(45deg,#c21500,#ffc500,#536976);
    background-repeat: repeat;
    color: #203A43;
    display: flex;
    align-items: center;
    justify-content: center;
}
#HomePage-title {
  margin-top: 5rem;
  margin-bottom: 1rem;
  font-weight: bold;
  text-align: center;
}
  #carousel {
    width: 100%;
  }
  .carousel__container {
    margin-top: 1rem;
    padding: 3rem 3rem 2.5rem 3rem;
    overflow: auto;
    width: 100%;
    margin-bottom: 1rem;
    white-space: nowrap;
  }
  .carousel__container::-webkit-scrollbar {
    height: 1rem;
  }
  .carousel__container::-webkit-scrollbar-thumb {
    width: 2rem;
    background: #A3E4D7;
    border-radius: 0.5rem;
  }
  .carousel__container:hover .carousel__container-item {
    opacity: 0.9;
  }
  .carousel__container:hover .carousel__container-item:hover {
    opacity: 1;
    transform: scale(1.3);
  }
  .carousel__container-item {
    width: 13rem;
    height: 6rem;
    border: 2px solid #96DBF1;
    border-radius: 25px;
    display: inline-block;
    margin-right: 10px;
    transform-origin: center left;
    transition: 450ms all;
    overflow: hidden;
    cursor: pointer;
  }
  .carousel__container-item:hover ~ .carousel__container-item{
    transform: translate3d(90px, 0, 0);
  }
  .carousel__container-item__img {
    width: 13rem;
    height: 6.5rem;
    object-fit: cover;
  }
  #servicios {
    padding: 2rem;
  }
  #servicios h1 {
    text-align: center;
    margin-top: 7rem;
    margin-bottom: 2rem;
  }
  #footer {
    width: 100%; 
    color: white;
    text-align: center;
  }
  #footer .row{
    background: linear-gradient(to right, #0f2027 60%, #203a43, #2c5364);
    padding: 2rem;
    padding-top: 6rem;
  }
  #footer .container-fluid img{
    width: 3rem;
    height: 3rem;
  }
  .mapa {
    border-radius: 10%;
  }

  @media only screen and(max-width: 1090px) {
    .carousel__container {
      display: block;
      justify-content: center;
      margin-bottom: 1rem;
      white-space: wrap;
    }
  }
  

  
  
