    font-weight: bold;
}
 
.Card-links {
    font-size: 1.3rem;
    text-decoration: none;
    color: aquamarine;
}

.Card-links a { 
    color: inherit; 
} 
.Precios-title {
    text-align: center;
    margin-bottom: 3rem;
}
